import React from "react";
import logo from "./logo.svg";
import "./App.css";

import { Switch, Route } from "react-router-dom";

import Main from "./components/Main";
import BecomeDriver from "./components/BecomeDriver";
import BecomeDriverRegistration from "./components/BecomeDriverRegistration";
import BecomeDeliveryResitration from "./components/BecomeDeliveryResitration";
import BecomeMerchantRegistration from "./components/BecomeMerchantRegistration";
import About from "./components/layout/About";
import SpecificService from "./components/SpecificService";
import Contact from "./components/layout/Contact";

function App() {
  return (
    <div className="App">
      <Switch>
        <Route exact path="/" component={Main} />
        <Route path="/become-driver" component={BecomeDriver} />
        <Route
          path="/become-driver-register"
          component={BecomeDriverRegistration}
        />
        <Route
          path="/become-delivery-register"
          component={BecomeDeliveryResitration}
        />
        <Route path="/become-merchant" component={BecomeMerchantRegistration} />
        <Route path="/about" component={About} />
        <Route path="/services/:type" component={SpecificService} />
        <Route path="/contact" component={Contact} />

        <Route
          path="/services/:type"
          render={(matchProps) => (
            <SpecificService
              {...matchProps}
              {...this.props}
              handleMatch={this.handleMatch}
            />
          )}
        />
      </Switch>
    </div>
  );
}

export default App;
